import * as React from "react";

import { BrowserRouter, Route } from "react-router-dom";

import { OspApp } from "./OspApp";
import { Provider } from "mobx-react";

export class App extends React.Component<{}, {}> {

    public render() {
        return (
            <Provider>
                <BrowserRouter >
                    <div>
                        <Route path="/" component={OspApp} />
                    </div>
                </BrowserRouter>
            </Provider>
        );
    }
}


